.main-header {
  padding: 5px;
  background-color: rgb(36, 135, 157);
  display: flex;
  justify-content: end;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

.main-header label {
  padding: 5px;
  background-color: rgb(36, 135, 157);
  font-size: 28px;
  color: white;
  font-family: "Oxygen";
  display: flex;
  justify-content: space-around;
  position: fixed;
  top: 0;
  left: 0;
}

.main-header button {
  margin-top: 5px;
  margin-left: 5px;
  margin-right: 15px;
  padding: 5px;
  cursor: pointer;
  transition: all 0.6s;
}

.main-header button:hover {
  background-color: #70b3be;
}

.main-aside {
  margin-top: 80px;
  margin-bottom: 80px;
  margin-left: 10px;
  width: 200px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  float: left;
  color: white;
}

.main-aside table {
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 10px;
  width: 700px;
  margin-left: auto;
  margin-right: auto;
  flex-wrap: wrap;
  flex-direction: column;
  float: left;
  color: white;
  border-collapse: collapse;
  text-align: center;
	vertical-align: middle;
  display: table;
  table-layout: fixed;
}

.main-aside caption {
  font-style: italic;
  font-size: 24px;
  text-align: left;
  color: white;
  margin-bottom: 16px;
}

.main-aside thead th { 
  display: table-cell;
  text-align: center;
  border: 1px solid white;
  padding: 8px;
}

.main-aside th, td {
  display: table-cell;
  text-align: center;
  border: 1px solid white;
  padding: 8px;
}

.main-aside tr {
  display: table-row-group;
  width: 700px;
}

.main-aside tr:nth-child(odd) {
  background-color: rgba(106, 118, 184, 0.7);
}

.main-aside tr:nth-child(even) {
  background-color: rgba(184, 106, 159, 0.7);
}

.main-aside thead {
  display: table-header-group;
  font-weight: bold;
  background-color: rgb(36, 135, 157);
  color: white; 
}

.main-aside table button {
  font-weight: bold;
  float: left;
  cursor: pointer;
  color: white;
  width: 30px;
  background-color: rgb(36, 135, 157);
  transition: all 0.6s;
}

.button_submit {
  font-weight: bold;
  float: left;
  color: white;
  width: 100px;
  background-color: rgb(36, 135, 157);
  margin-top: 5px;
  margin-left: 5px;
  padding: 5px;
  font-size: 20px;
  cursor: pointer;
  transition: all 0.6s;
}

.main-aside section {
  width: 350px;
  height: 350px;
  padding: 10px;
  background-color: grey;
  margin-bottom: 10px;
  /* background: url("./images/holland.jpeg"); */
  background-position: center;
  background-size: cover;
}

.main-aside section p {
  font-size: 20px;
  color: gray;
}

.post-form {
  background-color: #4a8191;
  color: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.annotation{
  color: white;
  font-size: 10px;
}

.word-link{
  color: blue;
  font-size: 10px;
  cursor: pointer;
}

.blog-link {
  text-decoration: none;
  color: white;
}

.error {
  color: red;
}

.show-all {
  display: flex;
  align-items: center;
  margin-bottom: 80px;
  justify-content: center;
}
